@keyframes pulse {
  0% {
    transform: scale(0.95);
  }
  50% {
    transform: scale(1);
  }
  100% {
    transform: scale(0.95);
  }
}
@mixin for-l-smartphone-down-portrait {
  @media (max-width: 600px) and (orientation: portrait) {
    @content;
  }
}

@mixin for-l-tablet-down {
  @media (max-width: 1024px) {
    @content;
  }
}

.button {
  min-width: 100px;
  width: auto;
  height: 40px;
  letter-spacing: 0.5px;
  line-height: 50px;
  padding: 0 35px;
  font-size: 15px;
  border-radius: 1rem;
  outline: none;
  background-color: black;
  color: white;
  text-transform: uppercase;
  font-weight: bolder;
  border: none;
  margin: 6px auto;
  z-index: 99;
  cursor: pointer;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border: 2px solid transparent;
  &:hover {
    background-color: white;
    color: black;
    border: 2px solid black;
  }

  &.button-facebook {
    background-color: #4267b2;
    margin: 0;

    &:hover {
      background-color: white;
      color: #4267b2;
      border: 2px solid #4267b2;
    }
  }

  &.button-google {
    background: #db4437;
    margin: 0;

    &:hover {
      background-color: white;
      color: #db4437;
      border: 2px solid #db4437;
    }
  }

  &.button-homepage {
    margin: 1.5rem 0;
    background-color: #163166;
    min-width: 250px;
    height: 90px;
    letter-spacing: 0.05em;
    font-size: 2rem;
    font-weight: normal;
    animation: pulse 1500ms infinite;
    &:hover {
      background-color: white;
      color: #163166;
      border: 4px solid #163166;
    }
    @include for-l-smartphone-down-portrait {
      font-size: 1.5rem;
      height: 80px;
      min-width: 200px;
      line-height: 40px;
    }
    @include for-l-tablet-down {
      font-size: 1.7rem;
      height: 90px;
      max-width: 220px;
    }
  }

  &.button-homepage-span {
    background-color: #163166;
    border-radius: 50%;
    width: 10vw;
    height: 10vw;

    &:hover {
      background-color: white;
      color: #163166;
      border: 4px solid #163166;
    }
  }
}
